@import url(https://fonts.googleapis.com/css?family=Lato);
#root {
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  margin: 20px;
}

.App {
  text-align: center;
  display: grid;
  grid-template-columns: 400px;
  justify-content: center;
}

.app-select {
  display: block;
  width: 400px;
  height: 50px;
  font-size: 15px;
  font-family: 'Lato', sans-serif;
}

.load-button {
  display:block;
  margin: 300px auto;
  width: 130px;
  height: 50px;
  background-color: white;
  border: 1px solid #a3a3a3;
  border-radius: 5px;
  box-shadow: 0 1px 5px #a3a3a3;
  font-size: 20px;
}

.load-button:hover {
  cursor: pointer;
  width: 140px;
  height: 60px;
  position: relative;
  font-size: 23px;
}

.todolist-table-headline {
  display: grid;
  grid-template-columns: 300px 700px;
  text-align: center;

}

.todolist-table {
  display: grid;
  grid-template-columns: 300px 700px ;
  grid-template-rows: 50px;
  justify-items: left;
  border: 1px solid #a3a3a3;
 }

.td-headline {
  display: inline-block;
  font-size: 25px;
  margin: 15px;

}

.todoitem {
  display: inline-block;
}

.todoitem_title {
  display: inline-block;

}

.todoitem_status {
  display: inline-block;
  font-size: 18px;
}

.progress {
  color: rgb(126, 35, 35);
  font-weight: 700;
}

.done {
  color: rgb(6, 134, 17);
  font-weight: 700;
}

.user {

}

.user_user-name {
margin: 5px 0;

}

.user_user-nickname {
  margin: 5px 0;
}

